*{
  padding: 0;
  margin: 0;
text-align: justify;
  text-decoration: none;

  box-sizing: border-box;

  
}

 a:visited {
  color: rgb(133, 85, 85);
}
a:active {
  color:rgb(66, 66, 145);
}
a:hover {
  color: rgb(219, 155, 95);
}

h3{
  color: #615929;
}
h4{
  color: #615929;
}
h1{
  color: #615929;
}
p{
  margin: 1rem;
  text-indent: 1rem;
  color: #615929;
  
}
/*=========================  Header section ====================================
================================================================================*/
.topSection h1, h4{
  font-family: 'Times New Roman', Times, serif;
}
.topSection{
  background-image: linear-gradient( 135deg,  #ded7b6 10%, #c2b88c 100%);
padding: 1rem 0.5rem 0;

}
.topSection .contact a{
  color: #615929;
}
.topSection .contact {
  color: #615929;
  
}
.header{
  margin-top: 2rem;
}

/*=========================  navbar section ====================================
================================================================================*/
.flex{
  display: flex;
  background-image: linear-gradient( 135deg, #faf8ed 10%, #dedbcc 100%);
}
.navbar{
  display: flex;
  list-style: none;
  background: #c28c3a;
  padding: .9rem;
  z-index: 100;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
  
}
.navbar li a{
  text-decoration: none;
  text-transform: uppercase;
  font-weight: bolder;
  color: #615929;
  font-family: x;
padding: .9rem;

 

}
.navbar a:hover{
 background:#ccc9b5 ;

 
 
}



.dropdown {
  position: relative;
  
}

.dropdown-content {
  display: none;
  margin-top: 15px;
  position: absolute;
  background-color: #deaf68;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

.dropdown-content a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}
.dropdown:hover .dropdown-content {display: block;}

/*======================== Time ===================
===================================================*/
.time{
margin-left:18rem;
}



/*=========================  Body section ====================================
================================================================================*/
.body{
  margin: 1rem 1rem 1rem 1rem;
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.15);
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-left: 1px solid rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(5px);
  border-radius: 10px;
}
.body h3{
margin: 1rem;
font-family: x;
}
.manuscript{
  margin-top: 4rem;
  color: #615929;
}
/*=========================  Announcements section =============================
================================================================================*/
.Announcement{
 margin: 1rem 1rem 1rem 0;
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.15);
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-left: 1px solid rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(5px);
  z-index: 12;
  border-radius: 10px;
  height: 32rem;
 

 
}
.Announcement h3{
  text-align: center;
  margin: 1rem;
font-family: x;
}
.Announcement a{
  color: #899233;
}
.Announcement ol {
  counter-reset: item;
  margin-left: 0;
  padding-left: 0;
  
}
.Announcement li {
  display: block;
  margin-bottom: .5em;
  margin-left: 2em;
  color: #615929;
}
.Announcement li::before {
  display: inline-block;
  content: counter(item) ") ";
  counter-increment: item;
  width: 2em;
  margin-left: -2em;
}
.Announcement a:visited {
  color: rgb(133, 85, 85);
}
.Announcement a:active {
  color:rgb(66, 66, 145);
}
.Announcement a:hover {
  color: rgb(219, 155, 95);
}
.notifications{
  padding: 1rem;
  z-index: 12;
  margin-top: .5rem;
height: 30rem;
  border-radius: 10px;
  overflow: auto;
}
/*=========================  Announcements scrollbar =============================
================================================================================*/

/* width */
.notifications::-webkit-scrollbar {
  width: 20px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 10px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #555; 
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #888; 
}



/*=========================  policy section ====================================
================================================================================*/
.ethicalpolicy{
  margin-top: 9rem;
}
/*=========================  Footer  ===========================================
================================================================================*/
.foot{
  background-image: linear-gradient( 135deg,  #bdb179 10%, #c2b88c 100%);
 font-family: x;
  z-index: 10;
  list-style-type: none;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
padding: .5rem;
  color: #615929;
}
.flexfoot {
  display: flex;
  justify-content: space-around;
  margin: .5rem 0 .5rem 0;
  font-family: x;
  font-weight: bolder;
 
 

}
.flexfoot a{
  color: #615929;
}

/*=========================  Media Queries =====================================
================================================================================*/

@media (max-width:1000px) {
  .navbar li a{
    font-size: small;
  
  }
  .time{
    margin-left: 3rem;
   
 transform: translateY(-.6rem);
height: 0;
    }
}
@media (max-width:700px) {
  
  .time{
    margin-left: 22rem;
 transform: translateY(-6.5rem);
height: 0;
    }
  
  .navbar{
    flex-direction: column;
    padding: 0;
    line-height: 1.4rem;
   
  }
  .navbar li a {
  font-size: small;
  margin-left: 1rem;
  padding: 2px;


 
  }
  .dropdown-content{
    margin-top: 0px;
  }

}
.main{
  flex: 1;
}
.sidebar{
  flex: .5;
}
@media (max-width:420px) {


  .time{
    margin-left: 14rem;

    }
   
}
.main{
  flex: 1;
}


@media (max-width:700px){

  .flex{
    flex-direction: column;
    
  }
  .flexfoot{
    flex-direction: column;
    padding: 1rem;
  text-align: center;
  }
  .flexfoot li{
    margin: 1rem;

  }
  
  }

